@media screen and (max-width:950px) {
    .footer{
        width: 85vw;
    }

    .our-socials{
        width: 75%;
    }
    .footer-newsletter-header{
        font-size: 24px;

    }



    .footer-navigation{
        width: 90%;
    }

    .footer-nav-links a{
        width: 5%;
    }

    .question{
        padding-left: 20px;
    }



}

@media screen and (min-height:800px ), ( max-width:828px){
}





@media screen and (max-width: 760px) {
    .footer{
        display: flex;
        flex-direction: column;
        align-items: unset;
        position: relative;
        
    }

    .links-group{
        width: 90%;
    }
    .social img{
        width: 28px;
    }
    .subscribe-to-news-footer{
        padding-bottom: 5%;
    }



    .footer-email-input{
        width: 70%;
    }

    .log-or-sign{
        padding-bottom: 10px;
    }




}



@media screen and (max-width:430px){

    .connect-header{
        font-size: 18px;
        line-height: 16px;
    }

    .social img{
        width: 28px;
    }

    .footer-email-input{
        padding: 20px 20px 20px 16px;
        width: 70%;
        font-size: 16px;
    }

    .footer-newsletter-header{
        font-size: 18px;
        padding-bottom: 15px;
        
    }

    .footer-nav-links a{
        font-size: 16px;
    }
    .log-or-sign span{
        padding-right: 20px;
    }


}

@media screen and (max-width:392px) {
    .footer-navigation{
        padding-top: 2vh;
        width: 100%;
    }
    .our-socials img{
        width: 28px;
    }



