.course{
    background: #F3E6FF;
}

.course-description{
    width: 80%;
    margin: auto;
}

.course-description-header{
    color: #0C0C0E;
    font-family: Merriweather;
    font-size: 40px;
    font-style: normal;
    font-weight: 700;
    line-height: 60px;
    padding-top: 3%;
}

.what-you-would-learn-header{
    color: #0C0C0E;
    font-family: bitter;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    padding-top: 2%;
}

.learning-outcome{
    padding-top: 2vh;
    width: 95%;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.learning-outcome + div{
    width: 40%;
    display: inline-flex;
    flex-direction: column;
}

.each{
    display: inline-flex;
    justify-content: space-between;
    padding-top: 10px;

}

.check{
    margin-top: 2px;
    margin-right: 10px;
    stroke-width: 20px;
    height: 20px;
    font-size: 30px;

}

.you-will-learn{
    list-style-type: none;
    color: #0C0C0E;
    font-family: Libre Franklin;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    width: 100%;
}



