.choose-path-header{
    color: var(--dark-light, #F1F1F3);
    text-align: center;
    font-family: Merriweather;
    font-size: 72px;
    font-style: normal;
    font-weight: 700;
    line-height: 112px;
}

/* .choose-path-header, .choose-path-text{
    margin-bottom: 12%;
} */

.choose-path-text{
    color: #F1F1F3;
    text-align: center;
    font-family: Libre Franklin;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 36px;
}



.choose-path{
    margin-bottom: 15vh;
}





@media screen and (max-width:600px){
    .course-description-header{
        font-size: 24px;
    }

    .what-you-would-learn-header{
        font-size: 16px;
    }


    .learning-outcome{
        display: block;
    }

    .check{
        font-size: 16px;
    }

    .each li{
        font-size: 12px;
    }



    .choose-path-header{
        text-align: center;
        font-family: Merriweather;
        font-size: 36px;
        font-style: normal;
        font-weight: 700;
        line-height: 43px; 
    }

    .choose-path-text{
        text-align: center;
        font-family: Libre Franklin;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 20px;
    }


    .choose-path{
        margin-bottom: 8vh;
    }

}